 enriquecido, pero respetuoso con su ritmo de aprendizaje.")]),_vm._m(4),_c('div',{staticClass:"bg-full-width bg-color-1 mb-5"},[_c('div',{staticClass:"px-4 p-md-5"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-7 mb-3 mb-lg-0"},[_c('h2',{staticClass:"mb-4",attrs:{"data-aos":"flip-up"}},[_vm._v("Ambientes en el desarrollo cognitivo de los niños")]),_vm._m(5),_c('a',{staticClass:"anexo mb-4 bg-white w-fit",attrs:{"href":_vm.obtenerLink('/downloads/Anexo_6.pdf'),"target":"_blank","data-aos":"flip-up"}},[_c('div',{staticClass:"anexo__icono",style:({'background-color': '#FCDFDB'})},[_c('img',{attrs:{"src":require("@/assets/template/icono-pdf.svg")}})]),_vm._m(6)])]),_vm._m(7)])])]),_vm._m(8),_c('p',{staticClass:"mb-4"},[_vm._v("El entorno en el que los niños aprenden y juegan influye directamente en su desarrollo cognitivo, emocional y social. Tanto el aula como el hogar pueden convertirse en espacios enriquecedores si se diseñan con materiales y recursos adecuados que fomenten la exploración, la creatividad y el aprendizaje significativo.")]),_c('div',{staticClass:"bg-full-width bg-color-1 mb-lg-5"},[_c('div',{staticClass:"px-4 p-md-5"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-7 order-2"},[_c('h2',{staticClass:"mb-4",attrs:{"data-aos":"flip-up"}},[_vm._v("Aplicación en el aula y en el hogar")]),_vm._m(9),_c('a',{staticClass:"anexo mb-4 bg-white w-fit",attrs:{"href":_vm.obtenerLink('/downloads/Anexo_7.pdf'),"target":"_blank","data-aos":"flip-up"}},[_c('div',{staticClass:"anexo__icono",style:({'background-color': '#FCDFDB'})},[_c('img',{attrs:{"src":require("@/assets/template/icono-pdf.svg")}})]),_vm._m(10)])]),_vm._m(11)])])]),_c('div',{staticClass:"bg-full-width border-top actividad bg-color-actividad"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('div',{attrs:{"id":"Actividad"}},[_c('Actividad',{attrs:{"cuestionario":_vm.cuestionario}})],1)])]),_c('div',{